<template>
  <div
    class="snackbar flex bg-success-color flex-col justify-center fixed sm:top-3/4 top-20 left-3 sm:left-12 w-96 sm:w-1/4 h-24 bg-custom-nav shadow animate-slideRightToLeft z-40"
    v-show="isOpenSnackBar"
    v-if="typeSnackBar == 'success'"
  >
    <div class="flex flex-row items-center justify-center z-30">
      <div class="p-1 rounded-full bg-white">
        <mdicon
          name="check"
          :width="16"
          :height="16"
          class="text-success-color"
        />
      </div>

      <div class="text-custom-white-text w-3/4 pl-2 text-left font-light">
        {{ messageSnackBar }}
      </div>
    </div>
    <div class="progress-container absolute top-20 w-full">
      <progress
        class="progress-success w-full h-2"
        max="200"
        :value="countUpValue"
      ></progress>
    </div>
  </div>
  <div
    class="snackbar flex bg-red-500 flex-col justify-center fixed sm:top-3/4 top-20 left-3 sm:left-12 w-96 sm:w-1/4 h-24 bg-custom-nav shadow animate-slideRightToLeft z-40"
    :class="{ dark: darkTheme }"
    v-show="isOpenSnackBar"
    v-if="typeSnackBar == 'error'"
  >
    <div class="flex flex-row items-center justify-center z-30">
      <div class="p-1 rounded-full bg-white">
        <mdicon name="close" :width="16" :height="16" class="text-red-500" />
      </div>
      <div class="text-custom-white-text w-3/4 pl-2 text-left font-light">
        {{ messageSnackBar }}
      </div>
    </div>
    <div class="progress-container absolute top-20 w-full">
      <progress
        class="progress-error w-full h-2"
        max="100"
        :value="countUpValue"
      ></progress>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  name: "SnackBar",
  data() {
    return {
      countUpValue: 0,
    };
  },
  methods: {
    ...mapActions({
      setIsOpenSnackBar: "setIsOpenSnackBar",
    }),
    countUpTimer() {
      if (this.countUpValue < 200) {
        setTimeout(() => {
          this.countUpValue += 1;
          this.countUpTimer();
        }, 20);
      } else {
        this.countUpValue = 0;
        this.setIsOpenSnackBar(false);
      }
    },
  },
  computed: {
    ...mapState([
      "isOpenSnackBar",
      "typeSnackBar",
      "messageSnackBar",
      "darkTheme",
    ]),
  },
  watch: {
    isOpenSnackBar() {
      if (this.isOpenSnackBar) this.countUpTimer();
    },
  },
};
</script>

<style lang="scss" scoped>
@use "@/assets/css/variables";

.progress-error::-webkit-progress-value {
  background: rgb(255, 0, 0);
}
.progress-success::-webkit-progress-value {
  background: rgb(23, 196, 0);
}
</style>
